.box {
    width: 30vw;
    height: auto;
    min-height: 41vh;
    max-height: inherit;
    background-color: #0f0f0f;
    position: relative;
    right: 7%;
    margin-top: 2%;
    border-radius: 2%;
    border: solid 2px whitesmoke;

}

.box2 {
    width: 20vw;
    height: auto;
    min-height: 83vh;
    max-height: inherit;
    background-color: #0f0f0f;
    position: absolute;
    left: 6%;
    top: 10%;
    border-radius: 2%;
    border: solid 2px whitesmoke;
}

#main {
    margin-left: 35%;
}

#main h1 {
    color: #fdfcfc;
}

button {
    position: absolute;
    top: 50%;
    left: 40.2%;
}

.text {
    color: white;
    padding: 2%;
    font-size: 25px;
}

.text2 {
    color: deeppink;
    padding: 2%;
    font-size: 21px;
    font-family: Courier;
}
